import { useEffect, useState } from "react";
import standingService from "../services/standingService";
import { useDispatch, useSelector } from "react-redux";

const StandingList = ({}) => {
  const dispatch = useDispatch();
  const [westernStanding, setWesternStanding] = useState([]);
  const [easternStanding, setEasternStanding] = useState([]);

  // const standings = useSelector((state) => state.standings.standings);

  useEffect(() => {
    standingService.getStandings().then((res) => {
      // dispatch(setUpcomingGames(res.data));
      setWesternStanding(res.data.Western);
      setEasternStanding(res.data.Eastern);
    });
  }, []);

  return (
    <div>
      <h1>Standings</h1>
      <h3>Eastern</h3>
      <table className={`standing-list`}>
        <thead>
          <tr>
            <th>Team</th>
            <th>GamesPlayed</th>
            <th>Wins</th>
            <th>Losses</th>
            <th>Points</th>
            <th>Last 10 Wins</th>
            <th>Last 10 Losses</th>
            <th>win%</th>
          </tr>
        </thead>
        <tbody>
          {easternStanding.map((eastTeam) => (
            <tr className={eastTeam.teamAbbrev} key={eastTeam.teamAbbrev}>
              <td>{eastTeam.homeCity}</td>
              <td>{eastTeam.gamesPlayed}</td>
              <td>{eastTeam.wins}</td>
              <td>{eastTeam.losses}</td>
              <td>{eastTeam.points}</td>
              <td>{eastTeam.l10wins}</td>
              <td>{eastTeam.l10losses}</td>
              <td>{Math.round(eastTeam.winPercentage * 100, 2)}</td>
            </tr>
          ))}
        </tbody>
      </table>
      <h3>Western</h3>
      <table className={`standing-list`}>
        <thead>
          <tr>
            <th>Team</th>
            <th>GamesPlayed</th>
            <th>Wins</th>
            <th>Losses</th>
            <th>Points</th>
            <th>Last 10 Wins</th>
            <th>Last 10 Losses</th>
            <th>win%</th>
          </tr>
        </thead>
        {westernStanding.map((westTeam) => (
          <tbody>
            <tr className={westTeam.teamAbbrev} key={westTeam.teamAbbrev}>
              <td>{westTeam.homeCity}</td>
              <td>{westTeam.gamesPlayed}</td>
              <td>{westTeam.wins}</td>
              <td>{westTeam.losses}</td>
              <td>{westTeam.points}</td>
              <td>{westTeam.l10wins}</td>
              <td>{westTeam.l10losses}</td>
              <td>{Math.round(westTeam.winPercentage * 100, 2)}</td>
            </tr>
          </tbody>
        ))}
      </table>
    </div>
  );
};

export default StandingList;
